@font-face {
  font-family: 'ui-bar-glyphs';
  src: url('../fonts/ui-bar-glyphs.eot?307853');
  src: url('../fonts/ui-bar-glyphs.eot?307853#iefix') format('embedded-opentype'),
       url('../fonts/ui-bar-glyphs.woff?307853') format('woff'),
       url('../fonts/ui-bar-glyphs.ttf?307853') format('truetype'),
       url('../fonts/ui-bar-glyphs.svg?307853#ui-bar-glyphs') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
